const store = configureStore({
  reducer: {
    game
  },
  preloadedState: {
    persistedState
  }
})

store.subscribe(() => {
  if (localStorage) {
    localStorage.setItem('ZombieChamber-GameSave', JSON.stringify(store.getState()))
  }
})


export default store
